@import '../../../../styles/variables.scss';

.this__day {
    padding: 20px;
    max-width: 400px;
    width: 100%;
    background-color: $lite-background;
    box-shadow: $box-shadow;
    border-radius: $border-radius;
}

.top__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    svg {
        width: 100px;
        height: 100px;
    }
    .this__temp {
        font-size: 96px;
        font-weight: 500;
        color: $primary-color;
    }

    .this__day_name {
        font-size: 40px;
        color: $lite-color;
    }
}

.bottom__block {
    .this__time,
    .this__city {
        font-size: 25px;
        color: $base-color;
    }
}